html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: grey;
  overflow-x: hidden; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  padding: 0;
  margin: 0 auto;
  font-family: "firasans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: black;
  background-color: white; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

@font-face {
  font-family: "firasans";
  src: local("firasans regular"), local("firasans-regular"), url("../fonts/firasans-regular.woff2") format("woff2"), url("../fonts/firasans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "firasans";
  src: local("firasans bold"), local("firasans-bold"), url("../fonts/firasans-bold.woff2") format("woff2"), url("../fonts/firasans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "firasans";
  src: local("firasans semibold"), local("firasans-semibold"), url("../fonts/firasans-semibold.woff2") format("woff2"), url("../fonts/firasans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "firasans";
  src: local("firasans medium"), local("firasans-medium"), url("../fonts/firasans-medium.woff2") format("woff2"), url("../fonts/firasans-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

.page-header {
